import React from 'react';
import './App.scss';


import Footer from './components/Footer';
const Header = React.lazy(() => import('./components/Header'))
const Banner = React.lazy(() => import('./components/Banner'))
const AboutMy = React.lazy(() => import('./components/AboutMy'))
const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'))
const RegisterToGetWork = React.lazy(() => import('./components/RegisterToGetWork/RegisterToGetWorkContainer'))

function App() {
  return (
    <div className="App">


      <React.Suspense fallback={<p>Loading...</p>}>
        <Header />
      </React.Suspense>

      <React.Suspense fallback={<p>Loading...</p>}>
        <Banner />
      </React.Suspense>

      <React.Suspense fallback={<p>Loading...</p>}>
        <AboutMy />
      </React.Suspense>


      <React.Suspense fallback={<p>Loading...</p>}>
        <UsersContainer />
      </React.Suspense>

      <React.Suspense fallback={<p>Loading...</p>}>
        <RegisterToGetWork />
      </React.Suspense>


      <Footer />

    </div>
  );
}

export default App;
